* {
  margin: 0;
  padding: 0;
  border: none;
  list-style: none;
}

.container{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2vh auto;
  max-width: 400px;
  min-height: 500px;
  padding: 3%;
  border-radius: 5%;
  background-color: #fff;
}

.title{
  font-size: 2.5rem;
  margin-bottom: 5%;
  color: #342F75;
}

.containerLogin{
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 80%;
}

.label{
  font-size: 1.2rem;
  margin: 15% 0 4% 0;
  color: #342F75;
  font-weight: bold;
}

.buttonLogin{
  justify-content: center;
  align-items: center;
  margin: 10vh auto 0 auto;
  width: 70px;
  height: 70px;
  font-size: 1.2rem;
  border-radius: 200rem;
  background-color: #1842F2;
  transition: 350ms;
  color: #f5ffff;
}

.buttonLogin:hover{
  transform: scale(1.2);
}

.buttonLogin:active{
  transition: 500ms;
  transform: scale(1.5);
}

.buttonDownload{
  justify-content: center;
  align-items: center;
  margin: 10vh auto 0 auto;
  width: 70px;
  height: 70px;
  font-size: 1.2rem;
  border-radius: 200rem;
  background-color: #F25D30;
  transition: 350ms;
  color: #f5ffff;
}

.buttonDownload:hover{
  transform: scale(1.2);
}

.buttonDownload:active{
  transition: 500ms;
  transform: scale(1.5);
}

.input{
  font-size: 1rem;
  padding: 3% 2%;
  border-bottom: 2px #000 solid;
}

.input:focus{
  outline: none;
  border-color: #004CA8;
}

@media screen and (max-width: 600px) {
  body {
    background-color: #fff;
  }
  .buttonLogin{
    margin-top: 22vh;
  }
}